import { createRouter, createWebHashHistory } from "vue-router";

/**
 *
 * Define constant isProActive to manipulate routes.
 * If premium addon is active, then isProActive will be true.
 *
 * @since 2.0.0
 */

const isProActive = false;
const isLicenceActive = false;

/**
 *
 * Default routes.
 *
 * @param {Array} defaultRoutes
 * @since 2.0.0
 */

const defaultRoutes = [];

/**
 *
 * Upsell routes.
 *
 * @param {Array} upsellRoutes
 * @since 2.0.0
 */

const upsellRoutes = [];

/**
 *
 * Routes for premium addon.
 *
 * @param {Array} proRoutes
 * @since 2.0.0
 */

const proRoutes = [];

/**
 *
 * Catch all routes.
 *
 * @param {Array} catchAllRoutes
 * @since 2.0.0
 */

const catchAllRoutes = [];

/**
 *
 * Define all routes for our app.
 *
 * @param {Array} proRoutes
 * @since 2.0.0
 */

const routes = [...defaultRoutes, ...proRoutes, ...catchAllRoutes];

const router = createRouter({
	history: createWebHashHistory(),
	routes,
});

export default router;
